<template>
  <div>
    <list
      :model="data">
      <div partial='a'>ddd {{data.name}} - {{data.parentid}} - {{data.size}}</div>
      <div partial='b'>ddd {{data.name}}</div>
    </list>
    <list
      :model="data2">
      <div partial='a'>{{data.name}} - {{data.age}}</div>
      <div partial='b'>{{data.name}}</div>
    </list>
    <button @click='add'></button>
  </div>
</template>

<script>
import List from './List'
import partial from '../plugins/partial.js'
// import Vue from 'vue'

// Vue.partial('test-partial', '{{data.name}} : {{data.age - 1}}')

export default partial({
  data () {
    return {
      data: [],
      data2: []
    }
  },
  asyncData () {
    return this.$http.post('/rs/sql/project.sql').then(
      a => ({data: a.data})
    )
  },
  methods: {
    add () {
      this.data2.push({name: 'afdsf', age: 40})
    }
  },
  components: {
    List
  }
})
</script>



