<template>
  <form @submit.prevent>
    <span>{{ parent.data.name }}</span>
    <div>
      <label for="name">:</label>
      <input id="name" type="text" v-model="data.name" placeholder="">
    </div>
    <button type="submit" @click="save"></button>
    <button></button>
  </form>
</template>

<script>
export default {
  props: ['parent'],
  methods: {
    save () {
      console.log('form save' + this.data)
      Object.assign(this.data, {parentid: this.parent.data.id})
      this.$http.post('/rs/entity/t_project', this.data).then(
        () => this.parent.reload()
      )
    }
  }
}
</script>



