<template>
  <div>
  <list
    :model="data"
    v-ref:list
    partial='testPartial'
    v-scope>
    <div>afsfdsfsdf</div>
  </list>
  <button @click='add'></button>
  </div>
</template>

<script>
import List from './List'
// Vue.partial('test-partial', '{{data.name}} : {{data.age - 1}}')

export default {
  data () {
    return {
      a: 'dfasdf',
      data: [
        {name: 'abc', age: 10},
        {name: 'def', age: 20},
        {name: 'dafds', age: 30}
      ]
    }
  },
  methods: {
    add () {
      this.data.push({name: 'afdsf', age: 40})
    }
  },
  components: {
    List
  }
}
</script>



