<template>
  <ul>
    <li v-for='model in data'>
      <tree :model='model' @selected="select"></tree>
    </li>
  </ul>
</template>

<script>
import TreeNode from '../tree-node'
import Tree from './Tree'

export default {
  data () {
    return {
      data: [],
      selected: null
    }
  },
  asyncData () {
    return this.$http.post('/rs/sql/project.sql').then(
      a => ({data: TreeNode.toTreeNode(a.data, '/rs/sql/subproject.sql')})
    )
  },
  methods: {
    select (data) {
      console.log('select ' + data)
      this.selected = data
    }
  },
  components: {
    Tree
  }
}
</script>



