export default {
  install (Vue) {
    Vue.directive('scope', {
      bind () {
        console.log(this.vm.$el.innerHTML)
        Object.assign(this.vm.$refs.list.$options.partials, {testPartial: '{{data.name}}'})
        // console.log('scope bind...' + this.vm.$options['partials']['test-partial'])
      },
      beforeCompile () {
        console.log('scope compiler')
      }
    })
  }
}



