import Vue from 'vue'

export default class TreeNode {
  // 
  static toTreeNode (data, path) {
    return Array.from(data, value => new TreeNode(value, path))
  }

  constructor (data, path, parent) {
    this.loaded = false
    this.data = data
    this.path = path
    this.parent = parent
    this.children = []
    this.size = data.size
  }

  // 
  addChild (data) {
    this.children.push(new TreeNode(data, this.path))
  }

  // 
  removeChild (data) {
    this.children.remove(data)
  }

  // 
  delete () {
    Vue.http.delete('/rs/entity/t_project/' + this.data.id).then(
      () => {
        alert('')
        this.parent.reload()
      }
    ).catch(
      (msg) => alert('' + msg)
    )
  }

  // 
  reload () {
    Vue.http.post(this.path, {id: this.data.id}).then(
      a => {
        console.log('loaded data' + a.data)
        this.children = Array.from(
          a.data, value => new TreeNode(value, this.path, this)
        )
        this.size = this.children.length
      }
    )
  }

  // 
  loadChild () {
    console.log('sub size is ' + this.children.length)

    if (this.loaded) {
      return
    }

    this.reload()
    this.loaded = true
  }
}



