<template>
  <div>
    <tree
      :model="data">
    </tree>
  </div>
</template>

<script>
import Tree from './Tree'
import List from '../list'

export default {
  data () {
    return {
      data: new List()
    }
  },
  components: {
    Tree
  }
}
</script>



