import $ from 'jquery'

export default function (obj) {
  return Object.assign({
    beforeCompile () {
      this.partials = new Map()
      $('[partial]', this.$el).each(
        (i, v) => {
          let hangup = v.parentElement.parentElement
          if (!this.partials.has(hangup)) {
            this.partials.set(hangup, {})
          }
          let partials = this.partials.get(hangup)
          let name = $(v).attr('partial')
          // partialdefault
          if (!name) {
            name = 'default'
          }
          console.log('partial name is' + name)
          partials[name] = v
        }
      )
    },

    compiled () {
      for (let v of this.$children) {
        let partials = this.partials.get(v.$el.parentElement)
        v.$options.partials = partials
      }
    }
  }, obj)
}



